import React, { useEffect } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";

import {
  Button,
  Grid,
  makeStyles,
  TextField,
  Paper,
  Divider,
  Typography,
  InputAdornment,
  CardMedia,
} from "@material-ui/core";

import { EventManager, trigger } from "../bridge";

import Card from "../Global/Card/Card.js";
import CardBody from "../Global/Card/CardBody.js";
import CardHeader from "../Global/Card/CardHeader";
import ReactLogo from "../../assets/logo512.png";

// Icons
import PersonAddSharpIcon from "@material-ui/icons/PersonAddSharp";
import ExitToAppSharpIcon from "@material-ui/icons/ExitToAppSharp";
import ArrowBackSharpIcon from "@material-ui/icons/ArrowBackSharp";
import ArrowForwardSharpIcon from "@material-ui/icons/ArrowForwardSharp";
import AccountCircleSharpIcon from "@material-ui/icons/AccountCircleSharp";
import LockOpenSharpIcon from "@material-ui/icons/LockOpenSharp";
import MailOutlineSharpIcon from "@material-ui/icons/MailOutlineSharp";

const useStyles = makeStyles({
  paper: {
    marginTop: "20vh",
    marginBottom: "20vh",
    margin: "auto",
    paddingLeft: "auto",
    maxWidth: "100vh",
  },
  grid: {
    marginLeft: "10px",
    marginRight: "10px",
    justify: "center",
    alignItems: "center",
    alignContent: "center",
  },
  card: {
    marginTop: "5em",
    marginBottom: "2em",
  },
});

function ASD(value: any) {
  alert(value);
}

export default function ShowPage() {
  const classes = useStyles();
  useEffect(() => {
    EventManager.addHandler("ASD", ASD);
    //trigger("ASD", "1");
  });

  return (
    <div>
      <Paper className={classes.paper}>
        <Grid
          container
          justify="center"
          alignItems="center"
          style={{ minHeight: "60vh" }}
        >
          <Grid item xs className={classes.grid}>
            <Card>
              <CardMedia component="img" image={ReactLogo} />
            </Card>
          </Grid>
          <Divider orientation="vertical" flexItem />
          <Grid item xs className={classes.grid}>
            <Login />
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}

const Login = () => {
  const classes = useStyles();
  const [toggled, setToggled] = React.useState(true);

  useEffect(() => {
    EventManager.addHandler("ASD", ASD);
  });

  const login = useFormik({
    initialValues: {
      name: "",
      password: "",
    },
    validationSchema: Yup.object({
      name: Yup.string().required("Это поле обязательно к заполнению."),
      password: Yup.string().required("Это поле обязательно к заполнению."),
    }),
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
  });

  const register = useFormik({
    initialValues: {
      name: "",
      email: "",
      password_1: "",
      password_2: "",
    },
    validationSchema: Yup.object({
      name: Yup.string()
        .required("Это поле обязательно к заполнению.")
        .min(4, "Минимальное кол-во символов - 4.")
        .max(16, "Максимальное кол-во символов - 16.")
        .matches(
          /^[a-zA-Z]+$/,
          "Поле может состоять только из букв латинского алфавита."
        ),
      email: Yup.string().email(),
      password_1: Yup.string()
        .required("Это поле обязательно к заполнению.")
        .min(6, "Минимальное кол-во символов - 6.")
        .max(16, "Максимальное кол-во символов - 16."),
      password_2: Yup.string()
        .required("Это поле обязательно к заполнению.")
        .oneOf([Yup.ref("password_1"), ""], "Пароль не совпадает."),
    }),
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
  });
  if (toggled) {
    return (
      <form onSubmit={login.handleSubmit}>
        <Grid container direction="column" alignItems="center" justify="center">
          <Card>
            <CardHeader color="danger">
              <Typography variant="h5" color="initial">
                Авторизация
              </Typography>
            </CardHeader>
            <CardBody>
              <TextField
                name="name"
                label="Логин"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={login.handleChange}
                value={login.values.name}
                error={login.errors.name ? true : false}
                helperText={login.errors.name}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <AccountCircleSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password"
                label="Пароль"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={login.handleChange}
                value={login.values.password}
                error={login.errors.password ? true : false}
                helperText={login.errors.password}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <Grid container direction="row" justify="flex-end" spacing={1}>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="primary"
                    type="submit"
                  >
                    <ExitToAppSharpIcon />
                    Вход
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="secondary"
                    onClick={() => setToggled((currToggled) => !currToggled)}
                  >
                    <PersonAddSharpIcon />
                    Регистрация
                  </Button>
                </Grid>
              </Grid>
            </CardBody>
          </Card>
        </Grid>
      </form>
    );
  } else {
    return (
      <form onSubmit={register.handleSubmit}>
        <Grid container direction="column" alignItems="center" justify="center">
          <Card className={classes.card}>
            <CardHeader color="danger">
              <Typography variant="h5" color="initial">
                Регистрация
              </Typography>
            </CardHeader>
            <CardBody>
              <TextField
                name="name"
                label="Логин"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.name}
                error={register.errors.name ? true : false}
                helperText={register.errors.name}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <AccountCircleSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="email"
                label="Email"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.email}
                error={register.errors.email ? true : false}
                helperText={register.errors.email}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <MailOutlineSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password_1"
                label="Пароль"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.password_1}
                error={register.errors.password_1 ? true : false}
                helperText={register.errors.password_1}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password_2"
                label="Подтвердите пароль"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.password_2}
                error={register.errors.password_2 ? true : false}
                helperText={register.errors.password_2}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharpIcon />
                    </InputAdornment>
                  ),
                }}
              />
              <Grid container direction="row" justify="flex-end" spacing={1}>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="primary"
                    type="submit"
                  >
                    <ArrowForwardSharpIcon />
                    Далее
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="secondary"
                    onClick={() => setToggled((currToggled) => !currToggled)}
                  >
                    <ArrowBackSharpIcon />
                    Назад
                  </Button>
                </Grid>
              </Grid>
            </CardBody>
          </Card>
        </Grid>
      </form>
    );
  }
};
